<style>
  @font-face {
    font-family: w5;
    src: url('./assets/font/w5.ttf');
  }
  html {
    font-size: 10vw;
    background: #f7f7f7;
  }
  @media screen and (min-width: 500px) {
    html {
      max-width: 500px;
      font-size: 50px;
    }
  }
  * {
    margin: auto;
    font-size: 0.35rem;
  }
  button {
    outline: none;
  }
  body {
    width: 100%;
    height: 100vh;
    background: white;
    padding: 0;
    margin: 0;
  }

  .main{
    margin-top: 1.2rem;
    margin-bottom: 1.2rem;
  }


  .grey {
    position: fixed;
    width: 100%;
    max-width: 500px;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 50%;
    margin-left: -5rem;
    z-index: 99;
  }
  input {
    outline: none;
  }
  textarea {
    outline: none;
  }
  a {
    outline: none;
  }
  select {
    outline: none;
  }



</style>



