<template>
    <div id="login">

      <Top></Top>
      <!--内容-->
      <div class="main">

        <div class="title">会员登录</div>
        <div class="form">
          <div class="input">
            <img src="../assets/img/user.png" alt="">
            <input type="text" placeholder="邮箱/用户名">
          </div>

          <div class="input">
            <img src="../assets/img/password.png" alt="">
            <input type="password" placeholder="登录密码">
          </div>

          <button class="button" onclick="location.href='index.html'">会员登录</button>

          <div class="options">
            <div @click="register">
              立即注册
            </div>
            <span></span>
            <div>
              忘记密码
            </div>


          </div>

        </div>

        <div class="tips">
          <div>如你无法完成注册</div>
          <div>可拨打客服热线：<span style="color: #c262d5;">440-9961-338</span></div>
          <div>会有客服专员帮你进行注册</div>
        </div>

      </div>
    </div>
</template>

<script>
  import Top from "./Top"
    export default {
        name: "login",
      components:{
          Top
      },
      methods:{
        register:function () {
          this.$router.push({name:"register"})
        }
      }
    }
</script>

<style scoped>
  .main {
    text-align: center;
    padding-top:2rem;
  }
  .main .title {
    text-align: center;
    font-size: 0.5rem;
    font-family: w5, '华文宋体';
    margin: 1rem;
    /*margin-top: 2rem;*/
  }
  .main .form .input {
    display: flex;
    width: 7rem;
    border: 1px solid #f2f2f2;
    padding: 0.1rem 0.5rem;
    margin-bottom: 0.3rem;
  }
  .main .form .input img {
    width: 0.5rem;
    height: 0.5rem;
    margin-right: 0.3rem;
  }
  .main .form .input input {
    flex: 1;
    border: 0;
    line-height: 0.8rem;
    outline: none;
    font-size: 0.4rem;
    font-family: w5, '华文宋体';
  }
  .main .form .input .code {
    color: #c262d5;
  }
  .main .form .button {
    border: 0;
    border-radius: 0;
    background: #c262d5;
    color: white;
    width: 8rem;
    height: 1.1rem;
    font-size: 0.4rem;
    font-family: w5, '华文宋体';
    margin-top: 0.2rem;
  }
  .main .form .options {
    display: flex;
    width: 8rem;
  }
  .main .form .options div {
    margin: 0.4rem;
    font-family: w5, '华文宋体';
    color: grey;
  }
  .main .form .options span {
    flex: 1;
  }
  .main .tips {
    position: fixed;
    bottom: 0;
    left: 50%;
    margin-left: -5rem;
    line-height: 0.6rem;
    width: 100%;
    max-width: 500px;
    font-family: w5, '华文宋体';
    margin-bottom: 0.5rem;
  }
  .main .form .checkbox {
    color: grey;
    text-align: center;
    margin: 0.3rem;
  }
  .main .form .checkbox input {
    width: 0.35rem;
    height: 0.35rem;
    position: relative;
    top: 0.07rem;
    outline: none;
  }

</style>



