<template>
    <div id="vipdetail">
      <Top></Top>
      <!--内容-->
      <div class="main">

        <div class="title">
          <div>
            <img src="../assets/img/vip_p.png" alt="">
          </div>
          <div>
            <div class="name">爆料先锋，临场绝杀</div>
            <div class="price">订阅价：1000币/日&nbsp;&nbsp;&nbsp;&nbsp;5000币/周&nbsp;&nbsp;&nbsp;&nbsp;15000币/月</div>
          </div>
        </div>

        <!--富文本框-->
        <div class="rich_content">

          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;">7月15日至16日，习近平来到壮丽北疆，就经济社会发展、生态文明建设和开展“不忘初心、牢记使命”主题教育情况进行考察调研。</p>
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;">正值第一批“不忘初心、牢记使命”主题教育深入开展的关键时刻，总书记走进内蒙古自治区自然资源厅，深询问、细指导，深入一线抓落实。</p>
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;">看到长条桌上摆放的一排相关学习材料和党组织活动记录，习近平强调：“我主要看实效，文件关键在执行。要强化法规制度执行，不能打折扣，防止文件发了之后，却都挂在墙上、堆在档案柜里。”</p>
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;">主题教育如何取得实际效果，解决实质问题？习近平强调的抓“四个到位”至关重要：抓思想认识到位，把学习贯穿始终，不断深化对主题教育重大意义的认识，深化对党的初心和使命的认识，深化对党面临的风险考验的认识；抓检视问题到位，深入调查研究，把问题找到找准，把根源挖深，明确努力方向和改进措施；抓整改落实到位，对症下药，该完善的体制机制要完善起来，该堵塞的漏洞要堵塞好，该批评的要认真批评，该处理的要严肃处理；抓组织领导到位，主要领导同志要带头学习、带头调查研究、带头检视问题、带头整改落实，发挥表率作用。</p>

        </div>

        <!--需要购买框，阻止内容滑动-->
        <div class="require">

          <!--内容-->
          <div class="content">
            <div class="score">
              <div>近5场成绩：
                <span style="color:black;">D</span>
                <span style="color:#c262d5;">W</span>
                <span style="color:#c262d5;">W</span>
                <span style="color:green;">L</span>
                <span style="color:#c262d5;">W</span>
              </div>
              <div @click="toMore">查看更多></div>
            </div>
            <div class="buy">
              <button>1000币/日</button>
              <button>5000币/周</button>
              <button>15000币/月</button>
            </div>
          </div>


        </div>

      </div>
    </div>
</template>

<script>
  import Top from "./Top"
    export default {
        name: "vip-detail",
      components:{
          Top,
      },
      methods:{
        toMore:function () {
          this.$router.push({name:'more_record'})
        }
      },
      mounted(){
        // 屏幕滑动
        document.querySelector('body').addEventListener('touchmove', function (event) {
          event.preventDefault()
        },{ passive: false });
        // 鼠标滑动
        document.querySelector('body').addEventListener('mousewheel', function (event) {
          event.preventDefault()
        },{ passive: false })
      }
    }
</script>

<style scoped>
  .main .title {
    display: flex;
    padding: 0.5rem;
  }
  .main .title div:first-child img {
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0.3rem;
  }
  .main .title div:last-child {
    flex: 1;
  }
  .main .title div:last-child div {
    line-height: 0.7rem;
    font-family: w5, '华文宋体';
  }
  .main .title div:last-child div:first-child {
    font-size: 0.35rem;
    color: black;
  }
  .main .title div:last-child div:last-child {
    font-size: 0.3rem;
    color: grey;
  }
  .main .rich_content {
    padding: 0.5rem;
  }
  .main .require .content {
    position: fixed;
    bottom: 0;
    left: 50%;
    margin-left: -5rem;
    width: 100%;
    max-width: 500px;
    z-index: 100;
  }
  .main .require .content .score {
    display: flex;
    background: #f1f1f1;
    padding: 0.3rem 0.5rem;
  }
  .main .require .content .score div:first-child {
    flex: 1;
  }
  .main .require .content .score div:last-child {
    color: grey;
    font-size: 0.3rem;
  }
  .main .require .content .buy {
    text-align: center;
    background: white;
    padding: 0.3rem;
  }
  .main .require .content .buy button {
    width: 8.5rem;
    line-height: 1rem;
    background: #c262d5;
    color: white;
    border: 0;
    border-radius: 0;
    margin: 0.2rem;
  }
  .main .require .content .buy button:nth-child(2) {
    background: #865cd4;
  }
  .main .require .content .buy button:nth-child(3) {
    background: #751a88;
  }

</style>



