<template>
    <div id="personnav">
      <Top></Top>
      <div class="nav">
        <div :class="{on:person_nav_index==1}" @click="nav_select(1)">个人资料</div>
        <div :class="{on:person_nav_index==2}" @click="nav_select(2)">发表记录</div>
        <div :class="{on:person_nav_index==3}" @click="nav_select(3)">发表咨询</div>
        <div :class="{on:person_nav_index==4}" @click="nav_select(4)">充值记录</div>
        <div :class="{on:person_nav_index==5}" @click="nav_select(5)">流水记录</div>
      </div>
    </div>
</template>

<script>
  import Top from "./Top"
    export default {
        name: "person-nav",
        components:{
          Top
        },
      props:["person_nav_index"],
      methods:{
        nav_select:function(index){
          if(index==1){
            this.$router.push({name:'person_info'})
          }
          if(index==2){
            this.$router.push({name:'person_push_record'})
          }
          if(index==3){
            this.$router.push({name:'person_push'})
          }
          if(index==4){
            this.$router.push({name:'person_recharge_record'})
          }
          if(index==5){
            this.$router.push({name:'person_diary'})
          }
        }
      }
    }
</script>

<style scoped>
  .nav {
    display: flex;
    background: #f2f2f2;
  }
  .nav .on {
    background: #c262d5;
    color: white;
  }
  .nav div {
    flex: 1;
    border-right: 1px solid white;
    text-align: center;
    padding: 0.3rem 0;
    font-family: w5, '华文宋体';
  }
  .nav div:last-child {
    border: 0;
  }
</style>



