<template>
  <div id="minenav">
    <Top></Top>
    <div class="mine_header">
      <div class="name">您好！康师傅</div>
      <div class="info">
        <div>D币余额：100</div>
        <div>数字ID：2568</div>
        <div>积分：10000</div>
      </div>
    </div>

    <div class="nav">
      <div :class="{on:mine_nav_index==1}" @click="mine_nav_select(1)">订单管理</div>
      <div :class="{on:mine_nav_index==2}" @click="mine_nav_select(2)">基本设置</div>
      <div :class="{on:mine_nav_index==3}" @click="mine_nav_select(3)">支付帮助</div>
      <div :class="{on:mine_nav_index==4}" @click="mine_nav_select(4)">帮助中心</div>
    </div>
  </div>
</template>

<script>
  import Top from "./Top"

  export default {
    name: "mine-nav",
    components: {
      Top,
    },
    props: ["mine_nav_index"],
    methods: {
      mine_nav_select: function (index) {
        if(index==1){
          this.$router.push({name:'mine_order'})
        }
        if(index==2){
          this.$router.push({name:'mine_info'})
        }
        if(index==3){
          this.$router.push({name:'mine_pay_help'})
        }
        if(index==4){
          this.$router.push({name:'mine_help_center'})
        }
      }
    }
  }
</script>

<style scoped>
  .nav {
    display: flex;
    background: #f2f2f2;
  }

  .nav .on {
    background: #c262d5;
    color: white;
  }

  .nav div {
    flex: 1;
    border-right: 1px solid white;
    text-align: center;
    padding: 0.3rem 0;
    font-family: w5, '华文宋体';
  }

  .nav div:last-child {
    border: 0;
  }

  .mine_header {
    padding: 0.4rem;
  }

  .mine_header .name {
    line-height: 0.8rem;
  }

  .mine_header .info {
    display: flex;
  }

  .mine_header .info div {
    flex: 1;
  }

  .mine_header .info div:nth-child(1) {
    text-align: left;
  }

  .mine_header .info div:nth-child(2) {
    text-align: center;
  }

  .mine_header .info div:nth-child(3) {
    text-align: right;
  }
</style>



