import Vue from 'vue'
import Router from 'vue-router'
import Vip from "../components/Vip"
import Blog from "../components/Blog"
import Playback from "../components/Playback"
import Immediate from "../components/Immediate"
import Information from "../components/Information"
import VipDetail from "../components/VipDetail"
import BlogDetail from "../components/BlogDetail"
import MorePlay from "../components/MorePlay"
import MoreInformation from "../components/MoreInformation"
import InformationDetail from "../components/InformationDetail"
import PersonInfo from "../components/PersonInfo"
import PersonPushRecord from "../components/PersonPushRecord"
import PersonPush from "../components/PersonPush"
import PersonDiary from "../components/PersonDiary"
import PersonRechargeRecord from "../components/PersonRechargeRecord"
import MineInfo from "../components/MineInfo"
import MineOrder from "../components/MineOrder"
import MinePayHelp from "../components/MinePayHelp"
import MineHelpCenter from "../components/MineHelpCenter"
import Login from "../components/Login"
import Register from "../components/Register"
import MoreRecord from "../components/MoreRecord"

// Uncaught (in promise) NavigationDuplicated {_name: "NavigationDuplicated", name: "NavigationDuplicated"}；  出错时补充以下代码
const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error=> error)
}

Vue.use(Router)
export default new Router({
  routes: [
    {path: '/', name:'vip', component:Vip},
    {path: '/blog', name:'blog', component:Blog},
    {path: '/playback', name:'playback', component:Playback},
    {path: '/immediate', name:'immediate', component:Immediate},
    {path: '/information', name:'information', component:Information},
    {path: '/vip_detail', name:'vip_detail', component:VipDetail},
    {path: '/blog_detail', name:'blog_detail', component:BlogDetail},
    {path: '/more_play', name:'more_play', component:MorePlay},
    {path: '/more_information', name:'more_information', component:MoreInformation},
    {path: '/information_detail', name:'information_detail', component:InformationDetail},
    {path: '/person_info', name:'person_info', component:PersonInfo},
    {path: '/person_push_record', name:'person_push_record', component:PersonPushRecord},
    {path: '/person_push', name:'person_push', component:PersonPush},
    {path: '/person_diary', name:'person_diary', component:PersonDiary},
    {path: '/person_recharge_record', name:'person_recharge_record', component:PersonRechargeRecord},
    {path: '/mine_info', name:'mine_info', component:MineInfo},
    {path: '/mine_order', name:'mine_order', component:MineOrder},
    {path: '/mine_pay_help', name:'mine_pay_help', component:MinePayHelp},
    {path: '/mine_help_center', name:'mine_help_center', component:MineHelpCenter},
    {path: '/more_record', name:'more_record', component:MoreRecord},
    {path: '/login', name:'login', component:Login},
    {path: '/register', name:'register', component:Register},
  ],
  // mode:'history'
})



