<template>
  <div id="top">
    <!--头部-->
    <header>
      <div class="title" @click="logo">赢足100</div>
      <div class="login" @click="login">登录</div>
      <div class="mine" @click="info">个人中心</div>
    </header>
  </div>
</template>

<script>
  export default {
    name: "top",
    methods: {
      logo: function () {
        this.$router.push({name:'vip'})
      },
      login: function () {
        this.$router.push({name:'login'})
      },
      info: function () {
        this.$router.push({name:'person_info'})
      }
    }
  }
</script>

<style scoped>
  header {
    height: 1.2rem;
    background: white;
    width: 100%;
    display: flex;
    border-bottom: 1px solid #f2f2f2;
    border-top: 1px solid #f2f2f2;
    position: fixed;
    top: 0;
    max-width: 500px;
  }

  header .title {
    font-size: 0.5rem;
    line-height: 1.2rem;
    flex: 1;
    margin-left: 0.5rem;
    font-weight: 600;
  }

  header .login {
    line-height: 1.2rem;
    margin-right: 0.5rem;
  }

  header .mine {
    line-height: 1.2rem;
    margin-right: 0.5rem;
  }
</style>



