<template>
  <div id="bottom">
    <!--脚部-->
    <footer>
        <span @click="toVip">
            <img v-if="nav_index == 1" src="../assets/img/vip2.png" alt="">
            <img v-if="nav_index != 1" src="../assets/img/vip.png" alt="">
            <p :class="{on:nav_index == 1}">vip推介</p>
        </span>
      <span @click="toBlog">
            <img v-if="nav_index == 2" src="../assets/img/home2.png" alt="">
          <img v-if="nav_index != 2" src="../assets/img/home.png" alt="">
            <p :class="{on:nav_index == 2}">博客推介</p>
        </span>
      <span @click="toPlayback">
            <img v-if="nav_index == 3" src="../assets/img/ago2.png" alt="">
          <img v-if="nav_index != 3" src="../assets/img/ago.png" alt="">
            <p :class="{on:nav_index == 3}">节目回放</p>
        </span>
      <span @click="toImmediate">
            <img v-if="nav_index == 4" src="../assets/img/now2.png" alt="">
          <img v-if="nav_index != 4" src="../assets/img/now.png" alt="">
            <p :class="{on:nav_index == 4}">即时比分</p>
        </span>
      <span @click="toInformation">
            <img v-if="nav_index == 5" src="../assets/img/basketball2.png" alt="">
          <img v-if="nav_index != 5" src="../assets/img/basketball.png" alt="">
            <p :class="{on:nav_index == 5}">体育资讯</p>
        </span>
    </footer>
  </div>
</template>

<script>

  export default {
    name: "bottom",
    props:["nav_index"],
    data() {
      return {

      }
    },
    methods: {

      // 连接式跳转
      // <router-link to="/">点击查看子页面</router-link>
      //   声明式跳转
      // <router-link :to="{path: '/', query:{id: 'abc'}}">点击查看子页面</router-link>
      // <router-link :to="{name: 'name', params:{id: 'abc'}}">点击查看子页面</router-link>
      // 编程式跳转
      // this.$router.push({path: '/',query:{id: 'abc'}})
      // this.$router.push({name: 'name',params:{id: 'abc'}})     // 这种方式需要在定义路由时配置名称

      toVip: function () {
        this.$router.push({name: 'vip'})
      },
      toBlog: function () {
        this.$router.push({name: 'blog'})
      },
      toPlayback: function () {
        this.$router.push({name: 'playback'})
      },
      toImmediate: function () {
        this.$router.push({name: 'immediate'})
      },
      toInformation: function () {
        this.$router.push({name: 'information'})
      },
    }
  }
</script>

<style scoped>
  footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    max-width: 500px;
    display: flex;
    height: 1.2rem;
    border-top: 1px solid #f2f2f2;
    background: white;
  }

  footer span {
    text-align: center;
    align-items: center;
    flex: 1;
  }

  footer span p {
    font-size: 0.2rem;
    margin-top: -0.1rem;
  }

  footer span img {
    width: 0.6rem;
    height: 0.6rem;
  }

  footer .on {
    color: #c262d5;
  }
</style>



