<template>
    <div id="mineorder">
      <!--内容-->
      <div class="main">

        <MineNav v-bind:mine_nav_index="1"></MineNav>

        <div class="content">

          <div class="selector">
            <button id="selector2">
              <span id="selector">推介订单服务</span>
              <img src="../assets/img/down.png" alt="">
            </button>
          </div>

          <div class="row">
            <div>订单号</div>
            <div>订单服务</div>
            <div>价格</div>
            <div>订购区域</div>
            <div>类型</div>
            <div>开始时间</div>
          </div>

          <div class="row">
            <div>11112352</div>
            <div>球赛</div>
            <div>10元</div>
            <div>广州</div>
            <div>比赛</div>
            <div>2019-08-17</div>
          </div>
          <div class="row">
            <div>11112352</div>
            <div>球赛</div>
            <div>10元</div>
            <div>广州</div>
            <div>比赛</div>
            <div>2019-08-17</div>
          </div>
          <div class="row">
            <div>11112352</div>
            <div>球赛</div>
            <div>10元</div>
            <div>广州</div>
            <div>比赛</div>
            <div>2019-08-17</div>
          </div>
          <div class="row">
            <div>11112352</div>
            <div>球赛</div>
            <div>10元</div>
            <div>广州</div>
            <div>比赛</div>
            <div>2019-08-17</div>
          </div>

          <div class="row">
            <div>11112352</div>
            <div>球赛</div>
            <div>10元</div>
            <div>广州</div>
            <div>比赛</div>
            <div>2019-08-17</div>
          </div>

        </div>

        <div class="pager">
          <button>上一页</button>
          <button>1</button>
          <button>下一页</button>
        </div>

        <div id="grey"></div>


      </div>
    </div>
</template>

<script>
  import MineNav from "./MineNav"
    export default {
        name: "mine-order",
      components:{
          MineNav,
      },
      mounted(){

          mui.init();

        var picker = new mui.PopPicker();
        // 给选择器设置内容
        picker.setData([
          {value:'1',text:'推介订单服务'},
          {value:'2',text:'推介订单服务'},
          {value:'3',text:'推介订单服务'},
          {value:'4',text:'推介订单服务'}
        ]);

        // 选择器(酒楼)
        document.querySelector('#selector').addEventListener('click',function () {
          picker.show(function (selectItems) {
            document.querySelector('#selector').innerHTML = selectItems[0].text;
          });
          // document.querySelector('#grey').setAttribute('class', 'grey');
        });

        // 监听点击取消事件
        document.querySelector('.mui-poppicker-btn-cancel').addEventListener('click',function () {
          document.querySelector('#grey').removeAttribute('class');
        });

        // 监听点击确认事件
        document.querySelector('.mui-poppicker-btn-ok').addEventListener('click',function () {
          document.querySelector('#grey').removeAttribute('class');
        });

        // 监听事件代触发
        document.querySelector('#selector2').addEventListener('click', function () {
          document.querySelector('#selector').click();
        });

      }
    }
</script>

<style scoped>
  .main .content .selector #selector2 {
    background: #c262d5;
    color: white;
    display: flex;
    border: 0;
    border-radius: 0.1rem;
    padding: 0.1rem;
    width: 4rem;
    margin-top: 0.5rem;
    margin-bottom: 0.4rem;
  }
  .main .content #selector2 img {
    width: 0.4rem;
    height: 0.4rem;
  }
  .main .content .row {
    display: flex;
    border-bottom: 1px solid #f2f2f2;
    line-height: 1rem;
    font-family: w5, '华文宋体';
  }
  .main .content .row div {
    font-size: 0.3rem;
    flex: 1;
    text-align: center;
  }
  .main .content .row:nth-child(2) div {
    font-size: 0.35rem;
    font-weight: bold;
  }
  .main .pager {
    text-align: center;
    margin: 0.5rem;
  }
  .main .pager button {
    border: 1px solid lightgrey;
    border-radius: 0;
    background: white;
    margin: 0.1rem;
    line-height: 0.6rem;
    padding: 0 0.3rem;
  }
</style>



