<template>
  <div id="mineinfo">
    <!--内容-->
    <div class="main">

      <MineNav v-bind:mine_nav_index="2"></MineNav>
      <div class="content">

        <div class="form">
          <div class="input">
            <div class="name">用户姓名</div>
            <div class="value">
              <input type="text" placeholder="填写用户名称">
            </div>
          </div>
          <div class="input">
            <div class="name">性别</div>
            <div class="value">
              <select>
                <option value ="male">男</option>
                <option value ="female">女</option>
              </select>
            </div>
          </div>
          <div class="input">
            <div class="name">旧密码</div>
            <div class="value">
              <input type="text" placeholder="选择性别">
            </div>
          </div>
          <div class="input">
            <div class="name">性别</div>
            <div class="value">
              <input type="text" placeholder="填写旧密码">
            </div>
          </div>
          <div class="input">
            <div class="name">新密码</div>
            <div class="value">
              <input type="text" placeholder="填写新密码">
            </div>
          </div>
          <div class="input">
            <div class="name">确认密码</div>
            <div class="value">
              <input type="text" placeholder="确认新密码">
            </div>
          </div>

          <div class="button">
            <button>确认</button>
            <button>重置</button>
          </div>
        </div>

      </div>



    </div>
  </div>
</template>

<script>
  import MineNav from "./MineNav"
    export default {
        name: "mine-info",
        components:{
          MineNav,
        }
    }
</script>

<style scoped>
  .main .content .form {
    padding: 0.5rem;
  }
  .main .content .form .input {
    display: flex;
    margin: 0.3rem 0;
    font-family: w5, '华文宋体';
  }
  .main .content .form .input .name {
    width: 2rem;
  }
  .main .content .form .input .value {
    flex: 1;
  }
  .main .content .form .input .value input,
  textarea {
    width: 90%;
    border: 1px solid #f2f2f2;
    font-size: 0.4rem;
    padding: 0.2rem 0.4rem;
    font-family: w5, '华文宋体';
  }
  .main .content .form .input .value select {
    width: 100%;
    border: 1px solid #f2f2f2;
    font-size: 0.4rem;
    padding: 0.2rem 0.4rem;
    font-family: w5, '华文宋体';
  }
  .main .content .form .button {
    display: flex;
    align-content: space-between;
  }
  .main .content .form .button button {
    flex: 1;
    background: #c262d5;
    color: white;
    border: 0;
    border-radius: 0.05rem;
    margin: 0.2rem;
    line-height: 1rem;
    font-size: 0.4rem;
    font-family: w5, '华文宋体';
  }
  .main .content .form .button button:last-child {
    background: #865cd4;
  }

</style>



