<template>
    <div id="personinfo">
      <div class="main">
        <PersonNav v-bind:person_nav_index="1"></PersonNav>
        <div class="form">
          <div class="input">
            <div class="name">会员类型</div>
            <div class="right" style="color:#c262d5;">个人</div>
          </div>
          <div class="input">
            <div class="name">用户头像</div>
            <div class="right">
              <div class="face">
                <img src="../assets/img/vip_p.png" alt="">
                <div>单张照片不大于500k</div>
              </div>
              <div class="selector" onclick="document.querySelector('#image').click()">
                <img src="../assets/img/mine.png" alt="">选择图片
              </div>
              <input id="image" type="file" accept="image/*" hidden="hidden">
            </div>
          </div>
          <div class="input">
            <div class="name">会员名称</div>
            <div class="right">
              <input type="text" placeholder="填写会员名称">
            </div>
          </div>
          <div class="input">
            <div class="name">原登录密码</div>
            <div class="right">
              <input type="password" placeholder="请填写原登录密码">
            </div>
          </div>
          <div class="input">
            <div class="name">新密码</div>
            <div class="right">
              <input type="password" placeholder="请填写新密码">
            </div>
          </div>
          <div class="input">
            <div class="name">确认新密码</div>
            <div class="right">
              <input type="password" placeholder="请填写确认密码">
            </div>
          </div>
          <div class="input">
            <div class="name">
              <span>个人简介</span>
            </div>
            <div class="right">
              <textarea name="" id="" cols="30" rows="10" placeholder="填写个人简介"></textarea>
            </div>
          </div>

          <div class="button">
            <button>保存</button>
          </div>

        </div>
      </div>

    </div>
</template>

<script>
  import PersonNav from "./PersonNav"
    export default {
        name: "person-info",
        components:{
          PersonNav,
        }
    }
</script>

<style scoped>
  .main .form {
    padding: 0.3rem;
  }
  .main .form .input {
    display: flex;
    font-family: w5, '华文宋体';
    padding: 0.2rem;
  }
  .main .form .input div:nth-child(1) {
    width: 2rem;
  }
  .main .form .input .right {
    flex: 1;
  }
  .main .form .input .right input {
    font-family: w5, '华文宋体';
    padding: 0.3rem;
    border: 1px solid #f2f2f2;
    border-radius: 0;
    outline: none;
    width: 90%;
  }
  .main .form .input .right textarea {
    font-family: w5, '华文宋体';
    padding: 0.3rem;
    border: 1px solid #f2f2f2;
    border-radius: 0;
    outline: none;
    width: 90%;
  }
  .main .form .input:nth-child(2) .right .face {
    display: flex;
    margin: 0;
    width: auto;
  }
  .main .form .input:nth-child(2) .right .face div {
    flex: 1;
    margin-left: 0.3rem;
    color: grey;
    font-size: 0.3rem;
  }
  .main .form .input:nth-child(2) .right .face img {
    width: 2rem;
    height: 2rem;
  }
  .main .form .input:nth-child(2) .right .selector {
    background: grey;
    color: white;
    line-height: 0.4rem;
    padding: 0.1rem;
    width: 2rem;
    text-align: center;
    font-size: 0.3rem;
    margin: 0.2rem 0;
  }
  .main .form .input:nth-child(2) .right .selector img {
    width: 0.4rem;
    height: 0.4rem;
    margin-right: 0.1rem;
    position: relative;
    top: 0.05rem;
  }
  .main .form .button {
    text-align: center;
  }
  .main .form button {
    background: #c262d5;
    color: white;
    width: 8rem;
    height: 1rem;
    border: 0;
    border-radius: 0.1rem;
    margin: 0.5rem;
  }
</style>



